.cruises {
    padding-top: 80px;
}

.cruises__wrap {
    width: 1130px;
    margin: 0 auto;
    margin-bottom: 110px;
}

.cruises__title {
    margin-bottom: 20px;
    text-align: center;
    font-weight: 600;
    font-size: 28px;
    line-height: 150%;
}

.cruises__list {
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    justify-content: space-between;
}

.cruises__item {
    max-width: 260px;
    margin-top: 30px;
    position: relative;
    background-color: $color-mobile--menu;
    color: $color-font--main;
    transition: all ease 0.7s;
}

.cruises__picture {
    position: relative;
    z-index: 2;
}

.cruises__item:hover .cruises__picture {
    opacity: 0;
    z-index: -1;
}

.cruises__head {
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
}

.cruises__head--close {
    position: absolute;
    bottom: 7px;
    left: 30px;
}

.cruises__head--open {
    margin-bottom: 15px;
}

.cruises__description {
    width: 210px;
    height: 330px;
    margin-top: 12px;
    margin-left: 25px;
    position: absolute;
    top: 0;
}

.cruises__details {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 140%;
}

.cruises dt {
    font-weight: 500;
}

.cruises dd {
    margin: 0;
    margin-bottom: 13px;
    color: $color-cruises;
}

.cruises__button {
    display: block;
    width: 165px;
    padding-top: 17px;
    padding-bottom: 10px;
    padding-left: 45px;
    background-color: $color-font--main;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: $color-mobile--menu;
}

@media (max-width: $tablet-width) {
    .cruises {
        padding-top: 70px;
    }

    .cruises__title {
        margin-bottom: 50px;
        font-size: 26px;
        text-shadow: 0px 4px 4px $color-shadow;
    }

    .cruises__item {
        max-width: 309px;
        margin-top: 0;
        margin-bottom: 26px;
        box-shadow: 0px 4px 4px $color-shadow;
    }

    .cruises__wrap {
        width: 648px;
        margin-bottom: 70px;
    }
}

@media (max-width: $mobile-width) {
    .cruises {
        padding-top: 40px;
    }

    .cruises__wrap {
        width: 270px;
        margin-bottom: 55px;
    }

    .cruises__title {
        margin-bottom: 0;
        font-size: 24px;
        text-shadow: none;
    }

    .cruises__item {
        width: 270px;
        box-shadow: none;
    }

    .cruises__list {
        margin-top: 0;
    }
}